<template lang="html">
  <van-tabbar v-model="active">
    <van-tabbar-item icon="home" to="/">首页</van-tabbar-item>
    <van-tabbar-item icon="shop" to="/category">分类</van-tabbar-item>
    <van-tabbar-item icon="cart" to="/car">购物车</van-tabbar-item>
    <van-tabbar-item icon="contact" to="/user">我的</van-tabbar-item>
    <van-tabbar-item icon="chat" to="/im" :dot="isNewChat">客服</van-tabbar-item>
  </van-tabbar>
</template>

<script>
import { Tabbar, TabbarItem,Toast} from 'vant';
import 'vant/lib/vant-css/index.css';

  export default {
  components:{
    [Tabbar.name]:Tabbar,
    [TabbarItem.name]:TabbarItem
  },
    methods: {
      gotoRouter() {
        this.$router.push({
          name: this.selected
        })
      }
    },
    data() {
      return {
        active: -1,
        isNewChat:false,
      }
    },
watch:{
    '$route':function(to,from){
        //this.routeName = to.name;
        this.$parent.$emit("changeTab");
    },
    '$store.state.im.count':function(){
        this.isNewChat=this.$store.state.im.count>0?true:false;
    }
},
    mounted() {

      this.$parent.$on("changeTab", function()
      {
          //获取当前路由名称，根据该名称给当前footer添加is-selected
          let Rname = this.$route.path;
          if(this.$route.name=='分类'){this.active = 1;}
          switch (Rname) {
            case '/':
              this.active = 0;
              break;
            case '/category':
              this.active = 1;
              break;
            case '/car':
              this.active = 2;
              break;
            case '/user':
              this.active = 3;
              break;
            case '/im':
              this.active = 4;
              break;
          }
      }.bind(this));

      this.$parent.$emit("changeTab");

    }

  }
</script>

<style lang="less" scoped>
.van-tabbar-item--active {
    color: #f23030;
}
.van-tabbar-item{cursor: pointer;}
.van-tabbar{
height: 55px;

  }
</style>



